import React from 'react';
import axios from "axios";
import SearchItem from "./SearchItem";
import './SearchBar.css';

class SearchBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            movies: []
        }
    }

    handleChange = async (e) => {
        source.cancel();

        if (e.target.value) {
            document.getElementsByClassName("searchbar")[0]
                .getElementsByClassName("items")[0].style.display = "block";
        }
        else {
            document.getElementsByClassName("searchbar")[0]
                .getElementsByClassName("items")[0].style.display = "none";
            return;
        }

        await axios.get(
            `${this.props.url}?query_term=${e.target.value}&limit=50`, {
                cancelToken: new axios.CancelToken(function executor(c) {
                    source.cancel = c;
                })
            }).then(({ data: { data: { movies } } }) => {
                this.setState({ movies });
            });
        }

    render() {
        const { movies } = this.state;
        return (
            <div className="searchbar">
                <input
                    type="text"
                    className="input"
                    placeholder="Search movies"
                    onChange={this.handleChange}
                />
                <div className="items">
                    {movies && 0 < movies.length &&
                    movies.map(item =>
                        <SearchItem
                            key={item.id}
                            title={item.title}
                            genres={item.genres}
                            rating={item.rating}
                            large_cover_image={item.large_cover_image}
                            year={item.year}
                            summary={item.summary}
                            yt_trailer_code={item.yt_trailer_code}
                            state={item.state}
                        />
                    )}
                </div>
            </div>
        );
    }
}

export default SearchBar;

const source = axios.CancelToken.source();